@use './vars';
@use './mixin';

// Change New-name par ton prénom
.david {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 27%;
    left: 65%;

    @include mixin.medium {
        font-size: 0.7em;
    }
    
    @include mixin.small {
        font-size: 0;
    }
}